* {
	webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.projects-button-spine {
	position: absolute;
	background: transparent;
	width: 100%;
	top: 20%;
}

.projects-button {
	height: 180px;
	width: 180px;
	border-radius: 100px;
	font-size: 45px;
	display: inline-block;
	position: relative;
	top: 20%;
	margin-left: 30px;
	margin-right: 30px;
	cursor: pointer;
	-webkit-transition: 0.2s;
	-o-transition: 0.2s;
	transition: 0.2s;
	background: black;
}

.pb1 {
	color: #FF1461;
	border: 5px solid #FF1461;
}
.pb1:hover {
	color: black;
	font-weight: bold;
	background: #FF1461;
}

.pb2 {
	color: #18FF92;
	border: 5px solid #18FF92;
}

.pb2:hover {
	color: black;
	font-weight: bold;
	background: #18FF92;
}
.pb3 {
	color: #5A87FF;
	border: 5px solid #5A87FF;
}
.pb3:hover {
	color: black;
	font-weight: bold;
	background: #5A87FF;
}

.projects-button-inner {
	width: 100%;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}









